import React, { useEffect } from 'react';
import Galeria from './Galeria';
import firstSlide from './imgs/gordito-barbon.jpg';
import secondSlide from './imgs/barber-vibe.jpg';
import thirdSlide from './imgs/barber-pole.jpg';
import axios from 'axios';

function Showroom() {
    
    const images = [ {name:"first", src:firstSlide}, {name:"second",src:secondSlide},{name:"third",src:thirdSlide}, {name:"fourth", src:firstSlide} ]

    useEffect(() => {
        const url = 'http://localhost:9000'
        
        const fetchData = async () => {
          const result = await axios(url + '/instagramAPI');
          console.log(result.data);
        };
     
        fetchData();
      }, []);

    return ( 
        <div className="Showroom">
            <div className="Container">
                <h2>GALERIA</h2>
                <Galeria images={images} />
            </div>
        </div>
    );
}
 
export default Showroom;